.header {
    background-color: #454545;
    padding: 10px 20px;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  
.brand {
  background-color: #454545;
  padding: 10px 20px;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
}


.hero {
  align-items: stretch;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
  
.container {
    margin: 0 auto;
      margin-top: 0px;
      margin-right: auto;
      margin-bottom: 0px;
      margin-left: auto;
    position: relative;
    width: auto;
}


.title {
  font-weight: 600;
  line-height: 1.125;
  word-break: break-word;
}


.section {
  display: block;
  padding: 2.15rem 1.5rem;
}